// Menggunakan import dinamis untuk HeroCarousel di HeroSection untuk menghindari circular dependency
// Import di sini hanya untuk tipe atau jika Anda menggunakan HeroCarousel di luar Suspense

// ========================
// üé≠ Tipe Data Sinkron
// ========================
// Menggunakan tipe Anime dari lib/api.ts untuk konsistensi
type AnimeWithDetails = Anime; 

// Fungsi Helper untuk menyesuaikan format respons API Consumet/Zoro
function extractAnimeArray(data: any): AnimeWithDetails[] {
  if (Array.isArray(data)) {
    return data;
  }
  // Asumsi respons non-array dari API memiliki properti 'data' (jika ada struktur respons berlapis)
  if (data && 'data' in data && Array.isArray(data.data)) {
    return data.data;
  }
  return [];
}


