// ========================
// üì∫ Sections
// ========================

async function HeroSection() {
  const data = await getTopAnime(1);
  const animes: AnimeWithDetails[] = extractAnimeArray(data).slice(0, 5);

  // Import dinamis untuk HeroCarousel
  const { HeroCarousel } = await import('@/components/hero-carousel'); 

  return (
    <div className="mt-8 mb-12 max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
      {/* Menggunakan 'as any' hanya untuk menenangkan TypeScript karena tipe di HeroCarousel
          mungkin sedikit berbeda dari tipe Anime di lib/api.ts. */}
      <HeroCarousel animes={animes as any} /> 
    </div>
  )
}

async function TopSection() {
  const data = await getTopAnime(1);
  const animes: AnimeWithDetails[] = extractAnimeArray(data).slice(0, 5);

  return (
    <div className="mb-12 max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
      <h2 className="text-xl sm:text-2xl font-bold text-foreground mb-3 sm:mb-4">
        Top Recent Anime
      </h2>

      <div
        className="
          grid grid-cols-2 sm:grid-cols-3 md:grid-cols-3 lg:grid-cols-5 gap-3 sm:gap-4 md:gap-6
        "
      >
        {animes.map((anime, index) => (
          <Link 
            key={anime.animeId || anime.mal_id || index} 
            // üî• FIX 404: Prioritaskan animeId Consumet/Zoro untuk link
            href={`/anime/${anime.animeId}`} 
          >
            {/* Menggunakan as any untuk kompatibilitas AnimeCard */}
            <AnimeCard anime={anime as any} /> 
          </Link>
        ))}
      </div>
    </div>
  )
}

